:root {
    --dark: #331B4B;
    --darkLight: #392a47;
    --light: #FFFADF;
    --grey: #A6A397;
    font-size: 18px;
    font-family: 'Poppins', sans-serif;
    color: var(--light);
  }
  
  
  html, body {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 100vw;
    min-height: 100vh;
    background-color: var(--dark);
  }
  
  /*
  Design du splash container
  */
  .splash-container {
    width: 350px;
    height: 350px;
    background-color: var(--light);
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--dark);
    transition: all .25s;
  }
  .splash-container h1 {
    font-family: 'Lobster', cursive;
    font-size: 3rem;
    text-align: center;
    transition: all .25s;
  }
  .splash-container.mini {
    width: 150px;
    height: 150px;
  }
  .splash-container.mini h1 {
    font-size: 1.6rem;
  }
  
  /*
  Barre de recherche
  */
  .search-bar {
    max-width: 400px;
    margin: 2rem;
    border: 1px solid var(--light);
    display: flex;
    align-items: center;
    border-radius: 5px;
    transition: all .25s;
    transform: translateX(-100vw);
    min-height: 0;
    margin: 0;
  }
  .search-bar.active {
    margin: 2rem;
    display: flex;
    transform: translateX(0px);
    min-height: auto;
  }
  .search-bar input {
    background-color: transparent;
    border: none;
    outline: none;
    padding: 1rem .6rem;
    font-size: 1rem;
    color: var(--light);
  }
  .search-bar button {
    display: flex;
    align-self: stretch;
    border-radius: 0;
    background-color: var(--light);
    color: var(--dark);
    font-size: 1rem;
    border: none;
    outline: none;
    padding: .6rem 1.3rem;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  /*
  Style pour le container des cocktails
  */
  .cocktail-container {
    padding: 1rem 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    max-width: 2000px;
  }
  
  
  /*
  Style pour un cocktail
  */
  .cocktail {
    display: flex;
    margin: .6rem;
    border-radius: 5px;
    align-items: center;
    overflow: hidden;
    background-color: var(--darkLight);
    cursor: pointer;
  }
  .cocktail .img-container {
    width: 230px;
    height: 230px;
    align-self: stretch;
    overflow: hidden;
  }
  .cocktail .img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .cocktail .detail {
    padding: 1rem 2rem;
  }
  
  @keyframes overlayAnimation {
    0% {
      max-height: 0;
    }
  
    100% {
      max-height: 100vh;
    }
  }
  
  @keyframes closeOverlayAnimation {
    0% {
      max-height: 100vh;
    }
  
    100% {
      max-height: 0;
    }
  }
  
  @keyframes leftPanelAnimation {
    0% {
      transform: translateX(-100%);
    }
  
    100% {
      transform: translateX(0);
    }
  }
  
  @keyframes closeLeftPanelAnimation {
    0% {
      transform: translateX(0);
    }
  
    100% {
      transform: translateX(-100%);
    }
  }
  
  /*
  Overlay et panneau de gauche
  */
  .overlay {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    backdrop-filter: blur(4px);
    display: flex;
    align-items: stretch;
    max-height: 0;
    overflow: hidden;
  }
  .overlay.active {
    animation-name: overlayAnimation;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  .overlay.disable {
    max-height: 100vh;
    animation-name: closeOverlayAnimation;
    animation-duration: .25s;
    animation-delay: .25s;
    animation-fill-mode: forwards;
  }
  
  .overlay .left-panel {
    background-color: var(--dark);
    width: 90%;
    transform: translateX(-100%);
    overflow: auto;
  }
  
  .overlay.active .left-panel {
    animation-name: leftPanelAnimation;
    animation-duration: .25s;
    animation-delay: .25s;
    animation-fill-mode: forwards;
  }
  .overlay.disable .left-panel {
    transform: translateX(0);
    animation-name: closeLeftPanelAnimation;
    animation-duration: .25s;
    animation-fill-mode: forwards;
  }
  
  .left-panel .top-bar {
    display: flex;
    align-items: center;
    padding: .6rem;
    cursor: pointer;
  }
  .left-panel .top-bar i {
    font-size: 2rem;
    margin-right: 1rem;
  }
  .left-panel .top-bar p {
    font-weight: bold;
  }
  
  .left-panel .img-container {
    align-self: stretch;
    height: 200px;
    overflow: hidden;
  }
  .left-panel .img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .left-panel h2 {
    font-family: 'Lobster', cursive;
    text-align: center;
    font-size: 2.4rem;
  }
  
  .left-panel .section {
    padding-left: .6rem;
    font-weight: bold;
  }
  
  .left-panel .instructions {
    padding: .6rem;
  }